d-color: #759493;\n    box-shadow: 0px 3px 0px 0px #465a58;\n}\n\n.btn-color-gold {\n    border: none;\n    color: white;\n    background: linear-gradient(-60deg, #FFE998, #bb7820, #FFE998, #bd7516);\n    box-shadow: 0px 3px 0px 0px #db8f2b;\n    background-size: 400% 400%;\n    animation: gradient 10s ease infinite !important;\n    height: 100vh;\n}\n\n@keyframes gradient {\n    0% {\n        background-position: 0% 50%;\n    }\n    50% {\n        background-position: 100% 50%;\n    }\n    100% {\n        background-position: 0% 50%;\n    }\n}\n\n@keyframes toNight {\n    0% {\n        background-position: 0% 50%;\n    }\n    100% {\n        background-position: 100% 50%;\n    }\n}\n\n@keyframes toDay {\n    0% {\n        background-position: 100% 50%;\n    }\n    100% {\n        background-position: 0% 50%;\n    }\n}\n\n.main-bg-color {\n    background: #380807;\n    background: -webkit-linear-gradient(290deg, #771e18, #311c1e, #410a09, #771e18);\n    background: linear-gradient(290deg, #771e18, #311c1e, #410a09, #771e18);\n    /* background: -webkit-linear-gradient(290deg, #101114, #313744, #555365, #775464);\n    background: linear-gradient(290deg, #101114, #313744, #555365, #775464); */\n    background-size: 200% 120%;\n    /* animation: gradient 21s ease infinite !important; */\n}\n\n.day .main-bg-color-2 {\n    background: #e0dada;\n    background: -webkit-linear-gradient(290deg, #410a09, #101114, #555365, #e0dada);\n    background: linear-gradient(290deg, #410a09, #101114, #555365, #e0dada);\n    background-size: 500% 120%;\n    background-position: 0% 50%;\n    animation: toDay 2s ease 1 !important;\n}\n\n.night .main-bg-color-2 {\n    background: #101114;\n    background: -webkit-linear-gradient(290deg, #410a09, #101114, #555365, #e0dada);\n    background: linear-gradient(290deg, #410a09, #101114, #555365, #e0dada);\n    background-size: 500% 120%;\n    background-position: 100% 50%;\n    animation: toNight 2s ease 1 !important;\n}\n\n\n/* box-shadow */\n\n.b-shadow-1 {\n    box-shadow: 0px 5px 20px 0px #00000063;\n}\n\n.b-shadow-2 {\n    box-shadow: 0px 3px 5px 0px #00000063;\n}\n\n.b-shadow-3 {\n    box-shadow: 0px 1px 1px 0px #00000063;\n}\n\n.b-shadow-4 {\n    box-shadow: 0px 5px 0px 0px #b1a9aa !important;\n}\n\n.b-shadow-5 {\n    box-shadow: 0px -3px 3px 0px #00000061;\n}\n\n.t-shadow-1 {\n    text-shadow: 0px 1px 1px black;\n}\n\n\n/* background color */\n\n.bg-color-white-t-lv1 {\n    background-color: #ffffff3d;\n}\n\n.bg-color-white-t-lv2 {\n    background-color: #ffffffe3;\n}\n\n.bg-color-theme-white {\n    background-color: #e1e4eb;\n}\n\n.bg-color-black {\n    background-color: rgba(37, 37, 37, 1);\n}\n\n.bg-color-black-t-lv1 {\n    background-color: rgba(37, 37, 37, 0.9);\n}\n\n.color-gray {\n    color: #4d4c4c;\n}\n\n.color-light-gray {\n    color: #b9b9b9d8;\n}\n\n.bg-color-gray {\n    background-color: #4d4c4c;\n}\n\n.bg-color-light-red {\n    background: #ede5e5;\n    background: -webkit-linear-gradient(290deg, #e0dada, #ede5e5);\n    background: linear-gradient(290deg, #e0dada, #ede5e5);\n}\n\n.bg-color-red {\n    background: #ee5253;\n    background: -webkit-linear-gradient(290deg, #ab5565, #ee5253);\n    background: linear-gradient(290deg, #ab5565, #ee5253);\n}\n\n.bg-color-red:enabled:active {\n    background: #7f474c!important;\n}\n\n.bg-color-red:disabled {\n    color: #a0a0a082!important;\n    background: #5c5c5c!important;\n}\n\n.bg-color-white-g {\n    background: #e1e4eb;\n    background: -webkit-linear-gradient(290deg, #b4bac7, white);\n    background: linear-gradient(290deg, #b4bac7, white);\n}\n\n.bg-color-black-g {\n    color: white;\n    background: rgba(37, 37, 37, 1);\n    background: -webkit-linear-gradient(290deg, rgba(37, 37, 37, 1), rgb(73, 73, 73));\n    background: linear-gradient(290deg, rgba(37, 37, 37, 1), rgb(73, 73, 73));\n}\n\n.bg-color-radial-gradient {\n    background: radial-gradient(ellipse at center, yellow, red);\n}\n\n.bg-color-black-t-lv1 {\n    background-color: rgba(37, 37, 37, 0.8);\n}\n\n.bdr-7 {\n    border-radius: 7px;\n}\n\n.bdr-12 {\n    border-radius: 12px;\n}\n\n.btn-color-light-red {\n    background: #ede5e5;\n    background: -webkit-linear-gradient(290deg, #e0dada, #ede5e5);\n    background: linear-gradient(290deg, #e0dada, #ede5e5);\n    box-shadow: 0px 3px 0px 0px #bdadb3!important;\n}\n\n.bg-color-wine-red {\n    /* background: -webkit-linear-gradient(290deg, #54444a, #5f4a4e);\n    background: linear-gradient(290deg, #54444a, #5f4a4e); */\n    background: #917076;\n    background: -webkit-linear-gradient(290deg, #7e666f, #917076);\n    background: linear-gradient(290deg, #7e666f, #917076);\n}\n\n.btn-color-wine-red {\n    /* background: -webkit-linear-gradient(290deg, #54444a, #5f4a4e);\n    background: linear-gradient(290deg, #54444a, #5f4a4e); */\n    background: #917076;\n    background: -webkit-linear-gradient(290deg, #7e666f, #917076);\n    background: linear-gradient(290deg, #7e666f, #917076);\n    box-shadow: 0px 3px 0px 0px #42353a!important;\n}\n\n.btn-color-wine-red:enabled:active {\n    margin-top: 2px!important;\n    background-color: #bdbdbd!important;\n    box-shadow: 0px 0px 0px 0px rgb(117, 117, 117)!important;\n}\n\n.btn-color-wine-red:disabled {\n    color: #d8d8d8!important;\n    background-color: #bbbbbb!important;\n    /* background: -webkit-linear-gradient(290deg, #c2c2c2, #bbbbbb, #bbbbbb);\n    background: linear-gradient(290deg, #c2c2c2, #bbbbbb, #bbbbbb); */\n    box-shadow: 0px 3px 0px 0px rgb(146 146 146)!important;\n}\n\n.btn-color-white {\n    border: none;\n    color: #424949 !important;\n    background-color: white!important;\n    /* background: -webkit-linear-gradient(290deg, #dadada, #ffffff, #ffffff);\n    background: linear-gradient(290deg, #dadada, #ffffff, #ffffff); */\n    box-shadow: 0px 2px 0px 0px rgb(182, 182, 182)!important;\n}\n\n.btn-color-white:enabled:active {\n    margin-top: 2px!important;\n    background-color: #bdbdbd!important;\n    box-shadow: 0px 0px 0px 0px rgb(117, 117, 117)!important;\n}\n\n.btn-color-white:disabled {\n    color: #d8d8d8!important;\n    background-color: #bbbbbb!important;\n    /* background: -webkit-linear-gradient(290deg, #c2c2c2, #bbbbbb, #bbbbbb);\n    background: linear-gradient(290deg, #c2c2c2, #bbbbbb, #bbbbbb); */\n    box-shadow: 0px 2px 0px 0px rgb(146 146 146)!important;\n}\n\n.btn-color-gray {\n    border: none;\n    color: #424949 !important;\n    background-color: rgb(202, 202, 202)!important;\n    box-shadow: 0px 2px 0px 0px rgb(136, 136, 136)!important;\n}\n\n.btn-color-red {\n    background-color: #ff4757;\n    box-shadow: 0px 3px 0px 0px #963535;\n    color: white;\n}\n\n.btn-color-yellow {\n    background-color: #fdcb6e;\n    box-shadow: 0px 3px 0px 0px #947741;\n    color: rgb(44, 44, 44);\n}\n\n.bg-color-deep-gray {\n    background-color: #424949;\n}\n\n.bg-color-red-mask {\n    background: linear-gradient(#ff4757, #c53a46f5);\n}\n\n.bg-color-black-mask {\n    background: linear-gradient(#727272, #242424ec);\n}\n\n.bg-color-black-mask-2 {\n    background: linear-gradient(rgba(25, 28, 23, 0.5), rgba(25, 28, 23, 0.6));\n}\n\n.bg-color-yellow-mask {\n    background: linear-gradient(rgba(254, 202, 87, 0.95), rgba(254, 202, 87, 0.8));\n}\n\n.bg-color-white-mask {\n    background: linear-gradient(rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.99));\n}\n\n.color-deep-gray {\n    color: #424949;\n}\n\n.color-white {\n    color: white;\n}\n\n.color-clould {\n    color: #ecf0f1;\n}\n\n.bg-color-clould {\n    background-color: #ecf0f1;\n}\n\n.color-green {\n    color: #1dd1a1;\n}\n\n.color-good {\n    color: #1abc9c;\n}\n\n.bg-color-green {\n    background-color: #1dd1a1;\n}\n\n.color-purple {\n    color: #9b59b6;\n}\n\n.bg-color-purple {\n    background-color: #9b59b6;\n}\n\n.color-yellow {\n    color: #feca57;\n}\n\n.wolves-color {\n    color: #ee5253;\n}\n\n.priesthood-color {\n    /* color: #feca57; */\n    color: #3867d6;\n}\n\n.villagers-color {\n    color: white;\n}\n\n.tune_svg_width {\n    width: 217px !important;\n}\n\n.lock-mobile-width {\n    max-width: 540px !important;\n    margin: auto;\n}\n\n.ht-10 {\n    height: 10px;\n}\n\n.ht-20 {\n    height: 20px;\n}\n\n.ht-30 {\n    height: 30px;\n}\n\n.ht-40 {\n    height: 40px;\n}\n\n.ht-50 {\n    height: 50px;\n}\n\n.ht-60 {\n    height: 60px;\n}\n\n.ht-100 {\n    height: 100px;\n}\n\n.ht-120 {\n    height: 120px;\n}\n\n.ht-130 {\n    height: 130px;\n}\n\n.wd-10 {\n    width: 10px;\n}\n\n.wd-20 {\n    width: 20px;\n}\n\n.wd-25 {\n    width: 25px;\n}\n\n.wd-30 {\n    width: 30px;\n}\n\n.wd-40 {\n    width: 40px;\n}\n\n.wd-50 {\n    width: 50px;\n}\n\n.wd-60 {\n    width: 60px;\n}\n\n.fs-7 {\n    font-size: 0.8rem !important;\n}\n\n.fs-8 {\n    font-size: 0.5rem !important;\n}\n\n.fs-3px {\n    font-size: 3px !important;\n}\n\n\n/* header */\n\n\n/* fixed to center */\n\n.main-menu-btn,\n.host-bar,\n.tips-bar,\n.error-message,\n.function-bar,\n.bottom-bar {\n    position: fixed;\n    left: 0;\n    right: 0;\n    margin: 0 auto;\n}\n\n.main-menu-btn {\n    top: 0;\n    height: 50px;\n    z-index: 2000;\n    width: 100%;\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    align-content: center;\n}\n\n.main-menu-btn-click {\n    color: #424242;\n    width: 30px;\n    height: 30px;\n    background-color: #ffffffe5;\n    border-radius: 25px;\n    box-shadow: 0px 1.5px 0.5px 0px rgb(146 145 145 / 90%);\n}\n\n.host-bar {\n    display: block;\n    top: 0px;\n    width: 100%;\n    height: 125px;\n    /* border-bottom: 0.1em dashed #9e9e9e; */\n    border-bottom: 2px solid #424949;\n    z-index: 1000;\n    overflow: hidden;\n}\n\n.tips-bar {\n    display: block;\n    top: 125px;\n    width: 100%;\n    min-height: 40px;\n    z-index: 1000;\n}\n\n\n/* \n.host-message {\n    position: relative;\n    display: flex;\n    vertical-align: middle;\n    align-content: center;\n    justify-content: center;\n    flex-direction: column;\n    height: 80px;\n    margin: 10px 10px 0px 10px;\n    border-radius: 30px;\n    background-color: #ffffffd4;\n} */\n\n.host-message-item {\n    letter-spacing: 0.1em;\n    border-top: 1px rgb(121, 121, 121) solid;\n    height: 90px;\n}\n\n.host-message-item.past {\n    margin-top: -55px;\n    color: rgb(165, 165, 165);\n}\n\n.host-text-arrow {\n    position: absolute;\n    bottom: -15px;\n    left: 40px;\n    width: 0;\n    height: 0;\n    border-left: 0px solid transparent;\n    border-right: 15px solid transparent;\n    border-top: 15px solid #ffffffd4;\n}\n\n.judge-icon {\n    display: flex;\n    position: absolute;\n    left: 5px;\n    top: 30px;\n    align-content: flex-start;\n    flex-direction: column;\n    animation: float 4s infinite;\n}\n\n.floating {\n    animation: float 2s infinite;\n}\n\n.now-arrow-icon {\n    text-shadow: 2px 2px 2px #00000042;\n    font-size: 50px;\n    margin-bottom: -15px;\n}\n\n.now-tips-tag {\n    width: fit-content;\n    padding: 0 4px;\n    border-radius: 50px;\n    box-shadow: 2px 2px 2px #00000042;\n}\n\n.fade-in {\n    animation: fadein 0.2s 1;\n}\n\n@keyframes fadein {\n    from {\n        opacity: 0;\n    }\n    to {\n        opacity: 1;\n    }\n}\n\n@-moz-keyframes fadein {\n    from {\n        opacity: 0;\n    }\n    to {\n        opacity: 1;\n    }\n}\n\n@-webkit-keyframes fadein {\n    from {\n        opacity: 0;\n    }\n    to {\n        opacity: 1;\n    }\n}\n\n.host-tips {\n    display: inline-flex;\n    vertical-align: middle;\n    align-content: center;\n    justify-content: center;\n    flex-direction: column;\n    font-size: 1rem;\n    color: white;\n    text-shadow: 0px 1px 1px black;\n    animation: bounceAfter 5s infinite;\n}\n\n@keyframes bounceAfter {\n    0% {\n        transform: translateY(0);\n    }\n    92% {\n        transform: translateY(0);\n    }\n    93% {\n        transform: translateY(-5px);\n    }\n    95% {\n        transform: translateY(3px);\n    }\n    97% {\n        transform: translateY(-2px);\n    }\n    100% {\n        transform: translateY(0);\n    }\n}\n\n@-moz-keyframes bounceAfter {\n    0% {\n        transform: translateY(0);\n    }\n    90% {\n        transform: translateY(0);\n    }\n    92% {\n        transform: translateY(-4px);\n    }\n    94% {\n        transform: translateY(2px);\n    }\n    96% {\n        transform: translateY(-1px);\n    }\n    100% {\n        transform: translateY(0);\n    }\n}\n\n@-webkit-keyframes bounceAfter {\n    0% {\n        transform: translateY(0);\n    }\n    90% {\n        transform: translateY(0);\n    }\n    92% {\n        transform: translateY(-4px);\n    }\n    94% {\n        transform: translateY(2px);\n    }\n    96% {\n        transform: translateY(-1px);\n    }\n    100% {\n        transform: translateY(0);\n    }\n}\n\n@keyframes bounce {\n    0% {\n        transform: translateY(0);\n    }\n    20% {\n        transform: translateY(-5px);\n    }\n    40% {\n        transform: translateY(3px);\n    }\n    60% {\n        transform: translateY(-2px);\n    }\n    80% {\n        transform: translateY(0);\n    }\n    100% {\n        transform: translateY(0);\n    }\n}\n\n@-moz-keyframes bounce {\n    0% {\n        transform: translateY(0);\n    }\n    20% {\n        transform: translateY(-5px);\n    }\n    40% {\n        transform: translateY(3px);\n    }\n    60% {\n        transform: translateY(-2px);\n    }\n    80% {\n        transform: translateY(0);\n    }\n    100% {\n        transform: translateY(0);\n    }\n}\n\n@-webkit-keyframes bounce {\n    0% {\n        transform: translateY(0);\n    }\n    20% {\n        transform: translateY(-5px);\n    }\n    40% {\n        transform: translateY(3px);\n    }\n    60% {\n        transform: translateY(-2px);\n    }\n    80% {\n        transform: translateY(0);\n    }\n    100% {\n        transform: translateY(0);\n    }\n}\n\n@keyframes float {\n    0% {\n        transform: translateY(0);\n    }\n    50% {\n        transform: translateY(5px);\n    }\n    100% {\n        transform: translateY(0)